/* eslint-env browser */
/* eslint-disable import/no-extraneous-dependencies, global-require */

import configure from '../client';

if (!location.pathname.match('/iframe.html')) {
  console.error(`storybook-chromatic should be installed in your \`.storybook/config.js\``);
}

const { __STORYBOOK_CLIENT_API__, __STORYBOOK_ADDONS_CHANNEL__ } = window;

const runtime = 'storybook';
configure({
  runtime,
  renderSpec({ runtime: specRuntime, input }) {
    if (specRuntime !== runtime) {
      throw new Error(`Storybook plugin cannot handle ${specRuntime} specs`);
    }

    const { kind, name } = input;
    __STORYBOOK_ADDONS_CHANNEL__.emit('setCurrentStory', { kind, story: name });

    // If the story has rendered with an error, SB does not return any kind of error
    // (we will fix this...) However, in the meantime, you can pick this up via a class on the body
    if (document.body.classList.contains('sb-show-errordisplay')) {
      const message = document.getElementById('error-message').textContent;
      const stack = document.getElementById('error-stack').textContent;
      const error = new Error(message);
      error.stack = stack;
      throw error;
    }

    return document.getElementById('root');
  },
  specs() {
    return __STORYBOOK_CLIENT_API__
      .getStorybook()
      .map(({ kind, stories }) =>
        stories.map(({ name }) => ({
          name,
          component: {
            name: kind,
            displayName: kind.split('/').slice(-1)[0],
          },
          runtime,
          input: JSON.stringify({
            kind,
            name,
          }),
        }))
      )
      .reduce((a, b) => [...a, ...b], []); // flatten
  },
});
